// 

export const getCombatType = function(type) {
  return GAME_TYPE[type]
}


// 

export const getAreaName = function(id) {
  for(var i = 0; i < AREAS.length; i++) {
    if (AREAS[i].id == id) {
      return AREAS[i].name
    }
  }
}

// news 
export const pv = function(val) {
  if (val > 10000) {
    return parseInt(val/10000) + ''
  } else {
    return val
  }
}

// iframesrc
export const videoSrc = function(url) {
  const newUserReg = /^src=/,
        store_id   = /src=/g,
        ticket   = /ticket/,
        isadminReg = /\?allowfullscreen/g,
        isRegisterReg = /\?register/g
  let arr = url.split(store_id)[1]
            .split(isadminReg)[0]
            .replace("allowfullscreen></iframe></div>", "")
            .split(/"/)[1]
  return arr
}


